form {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 400px;
    gap: 16px;
    align-items: right;
    height: 100%;
    margin-top: 1rem;
}

input, select {
    background-color: #EFEFEF;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1rem;
    color: #333333;
    border: none;
    padding: 16px;
    border-radius: 8px;
    -webkit-appearance: none;
    -moz-appearance: none;
}

form input.button {
    margin-top: 5%;
}

.button {
    text-align: center;
    cursor: pointer;
}
.hidden-button {
    background-color: #EFEFEF;
    text-decoration: none;
    color: #5e5e5e;
    padding: 16px 30px;
    border: none;
    border-radius: 8px;
    font-size: 1rem;
    font-weight: 600;
    display: inline-block;
    text-align: center;
    cursor: default;
}

.confirmation {
    display: flex;
    flex-direction:column;
    align-items: center;
    gap: 4rem;
    text-align: center;
    justify-content: center;
    height: 50vh;
}

.confirmation > .button {
    width: 50%;
}